// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);
$color-trans-25:                    rgba(0, 0, 0, 0.25);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-torch-red:                   #ff1553;
$color-tangaroa:                    #011c40;
$color-tangaroa-trans-70:           rgba(1, 28, 64, 0.7);
$color-tangaroa-trans-15:           rgba(1, 28, 64, 0.15);
$color-alice-blue:                  #f9fbfd;
$color-solitude:                    #e3ecf6;
$color-quartz:                      #d8d8d9;

// Gradient

$gradient-tangaroa:                 linear-gradient(180deg, $color-transparent 50%, $color-tangaroa 100%);

// Shadow

$shadow-book-form:                  0 4px 45px $color-tangaroa-trans-15;

// Typography
// ---------------------------------
$main-font: "Montserrat", arial, sans-serif;

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
// $ff-rouble:                         "Rouble", "Arial", sans-serif;

// $ff-placeholder:                    "Placeholder", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;

// Breakpoints, width, paddings

$mobile-width:                      320px;
$mobile-breakpoint:                 767px;
$mobile-padding:                    15px;

$tablet-width:                      768px;
$tablet-breakpoint:                 1023px;
$tablet-padding:                    45px;

$desktop-width:                     1024px;
$desktop-padding:                   103px;
$desktop-max-width:                 1366px;
